.airbnbNav {
  height: 70px;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.bold {
  font-weight: bold;
}

.gray {
  color: #918E9B;
}

.navLogo {
  max-width: 100px;
}

section {
  padding: 20px;
}
.hero {
  display: flex;
  flex-direction: column;
}

.heroPhoto {
  max-width: 400px;
  align-self: center;
}

.heroHeader {
  margin-bottom: 16px;
}

.heroText {
  margin-top: 0;
}

.cards-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
}

.card {
  width: 175px;
  font-size: 12px;
  flex: 0 0 auto;         /* ADD */
  display: flex;          /* ADD */
  flex-direction: column; /* ADD */
  position: relative;
}

.imageCard {
  width: 100%;
  border-radius: 9px;
  margin-bottom: 9px;
}

/* ADD */
.titleCard {
  overflow: hidden;
  text-overflow: ellipsis;
}

.statsCard {
  display: flex;
  align-items: center;
}

.starCard {
  height: 14px;
}

/* ADD */
.priceCard {
  margin-top: auto;
}

.badgeCard {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}